.header{
    background: #151515;
    position: relative;
    padding: 15px;
}

.header__logo{
    display: block;
    margin: 0 auto;
    width: 250px;
    height: auto;
    z-index: 1;
    position: relative;
}

.header__background{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    opacity: .3;
    background-image: url(/marvel-app/static/media/bg-header.1d1ae4b6.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

@media(max-width: 767px){
    .header__logo{
        width: 150px;
    }
}
.home__wrapper{
    max-width: 1280px;
    width: 100%;
    margin: 0 auto;
    padding: 0 15px;
}

.home__text{
    font-size: 16px;
    color: #151515;
    font-weight: 600;
    letter-spacing: 2px;
    display: block;
    text-align: center;
    margin: 30px 0;
}

