    letter-spacing: 2px;
    display: block;
    text-align: center;
    margin: 30px 0;
    text-transform: uppercase;
}

@media(max-width: 520px){
    .home__list-item{
        -webkit-flex: 1 1 100%;
                flex: 1 1 100%;
    }
}

@media(min-width: 991px){
    .home__list-item{
        opacity: .75;
    }
    
    .home__list-item:hover{
        -webkit-transform: translate3d(0, -25px, 0);
                transform: translate3d(0, -25px, 0);
        opacity: 1;
