.search__wrapper{
    max-width: 1280px;
    width: 100%;
    margin: 0 auto;
    padding: 0 15px;
}

.search__title{
    text-align: center;
}

.search__figure{
    max-width: 125px;
    margin: 0 auto;
}

.search__form{
    text-align: center;
    margin: 30px 0;
}

.search__form input{
    border: none;
    border-radius: 10px;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.35);
    height: 45px;
    width: 300px;
    outline: none;
    padding-left: 15px;
}

.search__form a{
    border: none;
    text-decoration: none;
    background: none;
    height: 45px;
    padding: 13px 10px;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.35);
    border-radius: 10px;
    margin-left: 10px;
    background: #151515;
    color: #fff;
    outline: none;
    -webkit-transition: all .3s;
    transition: all .3s;
    cursor: pointer;
}

.search__go-back{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    text-decoration: none;
    color: #151515!important;
    padding: 20px;
    font-weight: 600;
}

.search__go-back:before{
    content: '';
    display: block;
    width: 32px;
    height: 32px;
    background-image: url("data:image/png;base64,");
    background-size: 16px;
    background-position: center;
    background-repeat: no-repeat;
}

@media(max-width: 520px){
    .search__form input{
        width: 100%;
        text-align: center;
    }

    .search__form a{
        display: block;
        margin: 10px 0;
    }
}

@media(min-width: 991px){
    .search__form a{
        opacity: .75;
    }
    
    .search__form a:hover{
        opacity: 1;
    }
}
