.footer{
    padding-top: 80px;
    padding-bottom: 30px;
    background: #151515;
}

.footer__wrapper{
    max-width: 1280px;
    width: 100%;
    margin: 0 auto;
    padding: 0 15px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.footer__column{
    -webkit-flex: 0 0 auto;
            flex: 0 0 auto;
}

.footer__column a{
    color: #fff;
    font-size: 13px;
    font-weight: 400;
    text-decoration: none;
    letter-spacing: 2px;
    display: block;
    padding: 5px 0;
    -webkit-transition: all .3s;
    transition: all .3s;
}

.footer__column a:hover{
    -webkit-transform: translateY(-3px);
            transform: translateY(-3px)
}

.footer__column span{
    color: #fff;
    font-size: 16px;
    font-weight: 600;
    text-decoration: none;
    letter-spacing: 2px;
    display: block;
    padding: 5px 0;
}

.footer__copyright{
    -webkit-flex: 1 1 100%;
            flex: 1 1 100%;
    text-align: center;
